.home {
    text-align: center;
    padding: 10px;
    background-color: #ffffff;
    padding: 3rem;
  
  }
  
  .profile{
    display: flex;
  }
  /* Profile Image Styling */
  #profile__img{
    display: flex;
    margin: auto;
    position: relative;
    width: 88px;
    height: 88px;
    border-radius: 50%;
    background-image: url(../../../public/ProImg.jpg);
  }
  
  .share{
    font-size: 20px;
    width: 20px;
    height: 25px;
    border: 1px solid #D0D5DD;
    border-radius: 50%;
    padding: 5px;
    color: #D0D5DD;
    position: absolute;
    margin-left: 70%;
  }
  
  h1{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
    line-height: 30px;
    font-weight: 700;
    color: #101828;
    margin-bottom: 4rem;
  }
  
  /* links section */
  
  .links{
    display: grid;
    gap: 1rem;
  }
  
  #twitter{
    display: flex;
    justify-content: center;
    margin: auto;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 18px;
    line-height: 28px;
    font-weight: 500;
    font-style: medium;
    color: #101828;
    text-decoration: none;
    background-color: #EAECF0;
    padding: 24px;
    border-radius: 8px;
    width: calc(80%);
  }
